<template>
    <div id="edit">
        <Header2 :del="del" :del2="del2"  :FlagSignin=false :FlagAcc=false :FlagEdit=true :FlagAdded=false  />
        <div class="content">
            <div class="cont">
                <div class="cont1" v-for="(item,index) in list" :key="index" >
                    <div class="cont2">{{item.address_detail}}</div>
                    <div class="cont3">{{item.phone}}</div>
                    <p v-if="iNo" @click="dell(item.id,index) "><van-icon name="cross" /></p>
                </div>
            </div>
            <div class="con" @click="click()" >
                <div class="con1">新增地址</div>
                <van-icon name="arrow" class="con2"  />
            </div>
        </div>
    </div>
</template>

<script>

import Header2 from '../components/Header2' 
export default {
  components:{
        Header2
    },
  name: 'Edit',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      list:[],
      iNo:false
    }
  },
  created(){

  },
  methods:{
     click(){
         this.$router.push("./Added")
     },
     del(){
            // alert(1)
            this.iNo=true
    },
    del2(){
        this.iNo=false
    },
    dell(id,index){
        this.axios.delete(`http://elm.cangdu.org/v1/users/38184/addresses/${id}`).then(res=>{
            console.log(res.data)
            this.list.splice(index,1)

        })
    }
  },
  mounted(){
    this.axios.get('http://elm.cangdu.org/v1/users/38184/addresses').then(res=>{
        this.list=res.data
        console.log(res.data)
    })
    this.list=this.list.reverse() 
    console.log(this.list)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
#edit{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #F4F5F9;
    position: fixed;
    top: 0;
    background: #fff;
    z-index:5;
    .content{
        width: 100%;
        flex: 1;
        overflow: auto;
        margin-top: 2rem;
        .con{
            display:flex;
            line-height:2rem;
            background:white;
            position: relative;
            margin-top: 0.5rem;
            .con1{
                margin-left: 5%;
            }
            .con2{
                position: absolute;
                top:0.8rem;
                right:1rem;
            }
        }
        .cont{
            width: 100%;
            background: white;
            margin-top: 0.5rem;
            .cont1{
                width: 100%;
                border-bottom: 0.01rem solid gainsboro;
                position: relative;
                .cont2{
                    padding-top: 0.5rem;
                    margin-left: 5%;
                    width: 90%;
                }
                .cont3{
                    margin-top: 0.3rem;
                    margin-bottom: 0.5rem;
                    margin-left: 5%;
                    width: 90%;
                }
                p{
                    position: absolute;
                    top:1.2rem;
                    right: 1rem;
                }
            }
            .cont1:nth-of-type(1){
                background: #fff8c3;
            }
            
        }
    }
    
}
    
</style>



